.Header {
  padding: 0 12px;
  flex-direction: row;
  flex: 0 0 80px;
  height: 80px;
  display: flex; }
  @media print, screen and (min-width: 36em) {
    .Header {
      flex-basis: 80px;
      padding: 0; } }

.Header__group {
  flex: 1 1 auto;
  display: flex;
  align-items: center; }

.Header__group--left {
  margin-right: auto; }

.Header__group--right {
  margin-left: auto;
  justify-content: flex-end; }

.Header__logo {
  height: 40px;
  width: 40px;
  margin-right: 6px;
  display: none; }
  @media print, screen and (min-width: 36em) {
    .Header__logo {
      display: block; } }

.Header__logo__svg {
  height: 40px;
  width: 40px; }

.Header__title {
  color: #141414;
  font-size: 1rem;
  line-height: 1em; }
  .is-darkmode .Header__title {
    color: #fff; }
  .Header__title h1, .Header__title h2 {
    font-size: inherit;
    margin: 0;
    font-weight: normal; }

.Header__subtitle {
  font-size: 0.75rem !important; }

.Header a {
  color: inherit !important; }
