.Footer {
  padding: 0 12px;
  flex-direction: row;
  flex: 0 0 80px;
  height: 80px;
  display: none;
  color: #6f7275;
  opacity: 0.7;
  font-size: 0.75rem; }
  .is-darkmode .Footer {
    color: #cdd5e0; }
  @media print, screen and (min-width: 36em) {
    .Footer {
      flex-basis: 80px;
      padding: 0;
      display: flex;
      margin-bottom: 0px;
      flex-basis: 48px; } }
  .Footer a {
    border-bottom: 1px solid currentColor; }

.Footer__group {
  flex: 1 1 auto;
  display: flex;
  align-items: center; }

.Footer__group--left {
  margin-right: auto; }

.Footer__group--right {
  margin-left: auto;
  justify-content: flex-end; }
