@keyframes spin {
  100% {
    transform: rotate(360deg); } }

.FlipnoteThumb__image {
  position: relative;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: 100%;
  padding-bottom: 75%;
  overflow: hidden;
  width: 100%;
  border-radius: 4px; }

.FlipnoteThumb__loader {
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 28px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }
  .FlipnoteThumb__loader .icon {
    width: 32px;
    height: 32px; }
  .FlipnoteThumb__loader .icon svg.icon {
    animation: spin 1s linear infinite;
    fill: rgba(93, 106, 114, 0.5); }

.FlipnoteThumb__info {
  font-size: 0.85rem;
  line-height: 1rem;
  height: 28px;
  overflow: hidden;
  padding: 6px 0 3px 0;
  margin-top: -2px; }

.FlipnoteThumb__author {
  color: #fe7f7e; }

.FlipnoteThumb__type {
  position: absolute;
  right: 6px;
  bottom: 6px;
  display: inline-block;
  background: #efefef;
  color: #000;
  border-radius: 2px;
  padding: 0 6px;
  font-size: 0.7rem; }
